import React from "react";
import { useNavigate } from "react-router-dom";
import phrases from "./phrases";
import "./NotFound.css";
import notFoundImg from "../assets/images/notfound3.png";

// Este componente muestra una pgina de error 404 personalizada
// Utiliza frases divertidas relacionadas con la maternidad para hacer
// la experiencia ms cercana y menos frustrante
// Incluye una imagen ilustrativa y botones para navegar hacia atrs
// o volver a la pgina de inicio, facilitando que las usuarias
// encuentren su camino de vuelta

const NotFound = () => {
  const navigate = useNavigate();

  // Funcin para seleccionar una frase aleatoria del array de frases
  const getRandomPhrase = () => {
    const randomIndex = Math.floor(Math.random() * phrases.length);
    return phrases[randomIndex];
  };

  const randomPhrase = getRandomPhrase();

  return (
    <div className="container">
      <img src={notFoundImg} alt="Ilustracin divertida" className="image" />
      <h1 className="title">Oops! Algo se perdi...</h1>
      <p className="phrase">{randomPhrase}</p>
      <div className="button-container">
        <button
          className="button button-secondary"
          onClick={() => navigate(-1)}
        >
          Volver
        </button>
        <button className="button button-primary" onClick={() => navigate("/")}>
          Ir al inicio
        </button>
      </div>
    </div>
  );
};

export default NotFound;
