import React, { useState, useEffect } from "react";
import TruequeForm from "./TruequeForm";
import TruequeList from "./TruequeList";
import "./Trueque.css";
import { config } from "../config";
import truequesMockData from "../mock-data/trueques.json";
import usersData from "../mock-data/users.json";

// Este componente gestiona la funcionalidad de trueques
// Permite a los usuarios publicar, buscar y contactar por artculos de trueque.

const Trueque = () => {
  const [trueques, setTrueques] = useState([]);
  const [filterCity, setFilterCity] = useState("");
  const [isFormOpen, setIsFormOpen] = useState(false);

  // Funcin para obtener la lista de artculos disponibles desde el backend.
  // Incluye soporte para filtrar por ciudad o categora.
  const fetchTrueques = async (city = "") => {
    // Modo demo: usar datos mock
    if (config.useMocks) {
      let filteredTrueques = truequesMockData.map(trueque => ({
        ...trueque,
        user_name: usersData.find(u => u.id === trueque.user_id)?.name || "Usuario"
      }));
      
      if (city) {
        filteredTrueques = filteredTrueques.filter(t => 
          t.city.toLowerCase().includes(city.toLowerCase())
        );
      }
      
      setTrueques(filteredTrueques);
      return;
    }

    try {
      const response = await fetch(
        `http://localhost/mommatch/backend/get_trueques.php?city=${city}`
      );
      const data = await response.json();
      if (data.success) {
        setTrueques(data.trueques);
      } else {
        console.error("Error al obtener los trueques:", data.error);
      }
    } catch (error) {
      console.error("Error al conectar con el servidor:", error);
    }
  };

  useEffect(() => {
    fetchTrueques(filterCity);
  }, [filterCity]);

  const toggleForm = () => {
    setIsFormOpen(!isFormOpen);
  };

  // Estilos directos para mejorar el centrado
  const containerStyle = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    maxWidth: "1500px", // Aumentado de 1200px a 1500px
    margin: "0 auto",
    width: "100%",
  };

  return (
    <div className="trueque-container" style={containerStyle}>
      <h1 className="trueque-title">Zona de Trueque</h1>
      <button
        className="open-form-button compact"
        onClick={toggleForm}
        style={{ margin: "5px 0 15px 0" }}
      >
        {isFormOpen ? "Cerrar" : "Sube lo que quieras regalar o intercambiar"}
      </button>
      {isFormOpen && (
        <div className="trueque-form-modal">
          <TruequeForm onTruequeAdded={() => fetchTrueques(filterCity)} />
        </div>
      )}
      <div
        className="trueque-list-container"
        style={{ padding: "20px", width: "100%", maxWidth: "1500px" }}
      >
        {/* Lista de artculos trueque.
            + botones para contactar con el propietario del artculo. */}
        <TruequeList
          trueques={trueques}
          filterCity={filterCity}
          onFilterChange={setFilterCity}
        />
      </div>
    </div>
  );
};

export default Trueque;
