import React from "react";
import "./Conocenos.css";
import { useNavigate } from "react-router-dom";

// Esta pgina "Concenos" es informativa de la app MomMatch.
// Quera que fuera visualmente impactante pero tambin emotiva, para
// conectar con las mams.
//
// Trabaj mucho en la organizacin visual con imgenes decorativas que
// complementan el texto. El efecto de overlay en las imgenes mejora la
// legibilidad del texto superpuesto sin perder el impacto visual.
//
// Un pequeo detalle que aad es que al hacer clic en "nete ahora",
// primero hacemos scroll al inicio de la pgina antes de navegar, para
// que la experiencia sea ms fluida y la usuaria no se sienta desorientada.

const Conocenos = () => {
  const navigate = useNavigate();

  const handleJoinClick = () => {
    // Restablecer el scroll a la parte superior antes de navegar
    window.scrollTo(0, 0);
    document.body.scrollTop = 0; // Para Safari
    document.documentElement.scrollTop = 0; // Para Chrome, Firefox, IE y Opera

    // Navegar a la pgina de registro
    navigate("/register");
  };

  return (
    <div className="conocenos-container">
      {/* Agregar un pequeo script para forzar la recarga de imgenes */}
      <div style={{ display: "none" }}>
        {console.log("Forzando recarga de recursos: " + new Date().getTime())}
      </div>

      <div className="conocenos-hero">
        {/* Aadir el div para el GIF de fondo */}
        <div className="gif-background"></div>
        {/* Capa semitransparente para mejorar legibilidad */}
        <div className="overlay"></div>
        <h1 className="conocenos-title">Concenos</h1>
        <p className="conocenos-tagline">
          Porque la maternidad es mejor en compaa
        </p>
      </div>

      <div className="conocenos-content">
        <section className="conocenos-section">
          <h2 className="conocenos-subtitle">Nuestra Misin</h2>
          <p>
            <strong>MomMatch</strong> nace para crear una comunidad de apoyo
            entre madres. Creemos firmemente que{" "}
            <strong>la crianza es mejor en comunidad</strong> y que para cuidar
            bien de nuestros hijos, primero debemos cuidarnos a nosotras mismas.
          </p>
        </section>

        <section className="conocenos-section">
          <h2 className="conocenos-subtitle">Qu te ofrecemos?</h2>
          <div className="conocenos-offer-container">
            <div className="conocenos-cards">
              {/* Primera etapa - Espacio seguro */}
              <div className="conocenos-card">
                <h3>Espacio Seguro</h3>
                <p>
                  Un lugar libre de juicios donde expresarte con total libertad
                  y compartir tus experiencias sin miedo al qu dirn.
                </p>
              </div>

              {/* Imagen decorativa izquierda */}
              <div className="image-container">
                <img
                  src="/mommatch_demo/images/conocenos/conocenosL.jpg"
                  alt=""
                  className="decorative-image-left"
                  aria-hidden="true"
                />
                <div className="image-overlay">Comparte tu experiencia</div>
              </div>

              {/* Conexiones reales */}
              <div className="conocenos-card">
                <h3>Conexiones Reales</h3>
                <p>
                  Encuentra mams con intereses y experiencias similares a las
                  tuyas, creando vnculos significativos que perduran.
                </p>
              </div>

              {/* Imagen decorativa extra - en medio */}
              <div className="image-container">
                <img
                  src="/mommatch_demo/images/conocenos/hands.jpg"
                  alt=""
                  className="decorative-image-extra"
                  aria-hidden="true"
                />
                <div className="image-overlay">Unidas somos ms fuertes</div>
              </div>

              {/* Apoyo emocional */}
              <div className="conocenos-card">
                <h3>Apoyo Emocional</h3>
                <p>
                  Comparte tus alegras y desafos con quienes realmente te
                  entienden porque estn viviendo experiencias similares.
                </p>
              </div>

              {/* Imagen decorativa derecha */}
              <div className="image-container">
                <img
                  src="/mommatch_demo/images/conocenos/conocenosR.jpg"
                  alt=""
                  className="decorative-image-right"
                  aria-hidden="true"
                />
                <div className="image-overlay">Apoyo mutuo</div>
              </div>

              {/* Comunidad activa */}
              <div className="conocenos-card">
                <h3>Comunidad Activa</h3>
                <p>
                  Participa en eventos, intercambios y conversaciones
                  enriquecedoras que te ayudarn a crecer como madre y como
                  persona.
                </p>
              </div>

              {/* Imagen decorativa final */}
              <div className="image-container">
                <img
                  src="/mommatch_demo/images/conocenos/family.png"
                  alt=""
                  className="decorative-image-final"
                  aria-hidden="true"
                />
                <div className="image-overlay">Conexin en familia</div>
              </div>

              {/* Imagen panormica inferior */}
              <div className="image-container">
                <img
                  src="/mommatch_demo/images/conocenos/moms-together1.jpg"
                  alt=""
                  className="decorative-image-bottom"
                  aria-hidden="true"
                />
                <div className="image-overlay">
                  La maternidad es mejor cuando la compartimos
                </div>
              </div>

              {/* Cita inspiradora */}
              <div className="conocenos-quote">
                Cuando una madre abraza a otra con empata, el mundo se vuelve
                un lugar ms habitable.
              </div>
            </div>
          </div>
        </section>

        <section className="conocenos-section">
          <h2 className="conocenos-subtitle">Por qu unirte?</h2>
          <p>
            En <strong>MomMatch</strong> encontrars un espacio donde sentirte
            comprendida, apoyada y acompaada en tu camino como madre. Porque
            juntas somos ms fuertes. Porque juntas somos MomMatch.
          </p>
          <div className="button-container">
            <button className="button button-primary" onClick={handleJoinClick}>
              nete ahora
            </button>
          </div>
        </section>
      </div>
    </div>
  );
};

export default Conocenos;
