import React, { useState } from "react";
import "./Ayuda.css";
import config from "../config";

// Componente para la seccin de ayuda y soporte
// Incluye un formulario de contacto y preguntas frecuentes para resolver
// las dudas ms comunes de las usuarias.
//--
// La validacin del formulario fue interesante de implementar, sobre todo
// para dar feedback especfico en cada campo. Estuve pensando si usar
// una librera de validacin, pero al final lo hice manualmente
// teniendo ms control sobre los mensajes de error
//--
// El manejo de estados de envo (cargando, xito, error) me parece fundamental
// para una buena experiencia de usuario, as que aad mensajes claros
// para cada situacin

const Ayuda = () => {
  const [formData, setFormData] = useState({
    nombre: "",
    email: "",
    mensaje: "",
  });
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState(null);
  const [message, setMessage] = useState("");

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
    if (errors[name]) setErrors({ ...errors, [name]: null });
  };

  const validateForm = () => {
    const newErrors = {};
    if (!formData.nombre.trim()) newErrors.nombre = "Nombre requerido";
    if (!formData.email.trim()) {
      newErrors.email = "Email requerido";
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = "Email no vlido";
    }
    if (!formData.mensaje.trim()) newErrors.mensaje = "Mensaje requerido";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    setSubmitStatus(null);
    setMessage("");

    try {
      // En GitHub Pages, simular el envo sin hacer llamadas al backend
      if (config.useMocks) {
        // Simular delay de envo
        await new Promise((resolve) => setTimeout(resolve, 1000));
        setSubmitStatus("success");
        setMessage(
          "Mensaje simulado correctamente. (Versin demo - datos no persistentes)"
        );
        setFormData({ nombre: "", email: "", mensaje: "" });
        return;
      }

      // En desarrollo, hacer llamada real al backend
      const response = await fetch(
        `${config.backendUrl}/ayuda_api.php`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify(formData),
          credentials: "include",
        }
      );

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();

      if (data.success) {
        setSubmitStatus("success");
        setMessage("Mensaje enviado correctamente. Gracias por contactarnos.");
        setFormData({ nombre: "", email: "", mensaje: "" });
      } else {
        setSubmitStatus("error");
        setMessage(data.message || "Error al enviar el mensaje");
      }
    } catch (error) {
      console.error("Error:", error);
      setSubmitStatus("error");
      setMessage("Error al conectar con el servidor");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="mommatch-help-container">
      <div className="mommatch-help-header">
        <h1>Centro de Ayuda</h1>
        <p className="mommatch-help-intro">
          Estamos aqu para ayudarte, encuentra respuestas a tus preguntas o
          contctanos directamente
        </p>
      </div>

      {/* Nueva imagen de ayuda */}
      <div className="mommatch-help-image-container">
        <img
          src="/mommatch_demo/images/help/help.jpg"
          alt="Equipo de soporte MomMatch"
          className="mommatch-help-image"
        />
        <div className="mommatch-help-image-caption">
          Nuestro equipo est listo para asistirte en tu experiencia MomMatch
        </div>
      </div>

      <div className="mommatch-help-content">
        <section className="mommatch-help-faq">
          <h2>Preguntas Frecuentes</h2>
          <div className="mommatch-help-faq-list">
            <div className="mommatch-help-faq-item">
              <h3>Cmo me registro en la aplicacin?</h3>
              <p>
                Para registrarte, ve a la pgina de registro y completa el
                formulario con tus datos.
              </p>
            </div>
            <div className="mommatch-help-faq-item">
              <h3>Cmo accedo a los Matches?</h3>
              <p>
                Para acceder a los Matches, inicia sesin y ve a la seccin de
                "Matches" en el men principal.
              </p>
            </div>
          </div>
        </section>

        <section className="mommatch-help-contact">
          <h2>Contacto</h2>

          <form className="mommatch-help-form" onSubmit={handleSubmit}>
            <div
              className={`mommatch-help-form-group ${
                errors.nombre ? "has-error" : ""
              }`}
            >
              <label htmlFor="nombre">Nombre:</label>
              <input
                type="text"
                id="nombre"
                name="nombre"
                value={formData.nombre}
                onChange={handleChange}
              />
              {errors.nombre && (
                <span className="mommatch-help-error">{errors.nombre}</span>
              )}
            </div>

            <div
              className={`mommatch-help-form-group ${
                errors.email ? "has-error" : ""
              }`}
            >
              <label htmlFor="email">Email:</label>
              <input
                type="email"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
              />
              {errors.email && (
                <span className="mommatch-help-error">{errors.email}</span>
              )}
            </div>

            <div
              className={`mommatch-help-form-group ${
                errors.mensaje ? "has-error" : ""
              }`}
            >
              <label htmlFor="mensaje">Mensaje:</label>
              <textarea
                id="mensaje"
                name="mensaje"
                rows="5"
                value={formData.mensaje}
                onChange={handleChange}
              ></textarea>
              {errors.mensaje && (
                <span className="mommatch-help-error">{errors.mensaje}</span>
              )}
            </div>

            <div className="mommatch-help-submit">
              <button type="submit" disabled={isSubmitting}>
                {isSubmitting ? "Enviando..." : "Enviar Mensaje"}
              </button>

              {/* Mensaje de confirmacin justo debajo del botn */}
              {message && (
                <div
                  className={`mommatch-help-alert ${
                    submitStatus === "success" ? "success" : "error"
                  }`}
                >
                  {message}
                </div>
              )}
            </div>
          </form>
        </section>

        <footer className="mommatch-help-footer">
          <p>
            Necesitas ms ayuda? Escrbenos a{" "}
            <a href="mailto:soporte@mommatch.com">soporte@mommatch.com</a>
          </p>
          <div className="mommatch-help-social">
            <a
              href="https://facebook.com/mommatch"
              target="_blank"
              rel="noopener noreferrer"
            >
              Facebook
            </a>
            <a
              href="https://instagram.com/mommatch"
              target="_blank"
              rel="noopener noreferrer"
            >
              Instagram
            </a>
          </div>
        </footer>
      </div>
    </div>
  );
};

export default Ayuda;
