import React, { useState } from "react";
import "./HomePage.css";
import { useNavigate } from "react-router-dom";

// La pgina de inicio es la primera impresin que reciben las usuarias,
// as que quera que fuera atractiva y sencilla a la vez.
//--
// La estructura de dos columnas (imagen + registro) funciona bien en
// todas las pantallas y la animacin de carga de la imagen principal
// da un toque profesional sin complicar demasiado el cdigo.
//--
// Prob varias imgenes para la portada hasta encontrar una que transmitiera
// bien el concepto de conexin entre madres. Los botones de registro e inicio
// de sesin tienen estilos diferentes para dirigir a las nuevas usuarias
// hacia la opcin de registro.

function HomePage() {
  const navigate = useNavigate();
  const [imageLoaded, setImageLoaded] = useState(false);

  return (
    <div className="mommatch-homepage__container">
      {/* Seccin izquierda - Imagen */}
      <div className="mommatch-homepage__left-section">
        <img
          src="/mommatch_demo/Portada1.jpg"
          alt="Mams con bebs"
          className={`mommatch-homepage__main-image ${
            imageLoaded ? "loaded" : ""
          }`}
          onLoad={() => setImageLoaded(true)}
        />
      </div>

      {/* Seccin derecha - Registro */}
      <div className="mommatch-homepage__right-section">
        <h1 className="mommatch-homepage__title">Conecta con otras mams</h1>
        <p className="mommatch-homepage__subtitle">
          nete a la comunidad y encuentra tu red de apoyo
        </p>

        <button
          className="mommatch-homepage__email-btn"
          onClick={() => navigate("/register")}
        >
          Regstrate con tu email
        </button>

        <p className="mommatch-homepage__login-text">Ya tienes cuenta?</p>

        <button
          className="mommatch-homepage__login-btn"
          onClick={() => navigate("/login")}
        >
          Inicia sesin
        </button>
      </div>
    </div>
  );
}

export default HomePage;
