//Componente principal de la aplicacin MomMatch
//Este archivo define la estructura de rutas de la aplicacin,
//separando las rutas pblicas de las protegidas que requieren autenticacin.
//Utiliza React Router para la navegacin y AuthProvider para la gestin
//del estado de autenticacin en toda la aplicacin.

import "./App.css";
import "./styles/global.css";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import { AuthProvider } from "./context/AuthContext";
import Header from "./components/Header";
import ProtectedRoute from "./components/ProtectedRoute";
import ScrollToTop from "./components/ScrollToTop"; // Importar el componente ScrollToTop
import React, { useEffect, useState } from "react";
import { getDemoUser } from "./services/api";

// Componentes para rutas pblicas (accesibles sin iniciar sesin)
import HomePage from "./components/HomePage"; // Pgina de inicio/landing
import Conocenos from "./components/Conocenos"; // Informacin sobre la plataforma
import Blog from "./components/Blog"; // Blog de artculos
import Ayuda from "./components/Ayuda"; // Centro de ayuda
import Login from "./components/Login"; // Inicio de sesin
import Register from "./components/Register"; // Registro de nuevo usuario
import NotFound from "./components/NotFound"; // Pgina 404

// Componentes para rutas protegidas (requieren autenticacin)
import Perfil from "./components/Perfil"; // Editor de perfil
import PerfilVista from "./components/PerfilVista"; // Vista de perfil
import MatchPage from "./components/MatchPage"; // Sistema de matchmaking
import Comunidad from "./components/Comunidad"; // Hub de la comunidad
import Forum from "./components/Forum"; // Foro de discusin
import Trueque from "./components/Trueque"; // Zona de trueque
import TruequeMessages from "./components/TruequeMessages"; // Sistema de mensajera de trueques
import Events from "./components/Events"; // Pgina de eventos y quedadas

// Componentes para rutas de administracin
import AdminLogin from "./components/Admin/AdminLogin";
import AdminDashboard from "./components/Admin/AdminDashboard";

// Agregar un aviso global de que es una demo
const DemoBanner = () => (
  <div
    style={{
      position: "fixed",
      bottom: 0,
      width: "100%",
      backgroundColor: "rgba(255, 204, 0, 0.85)",
      color: "#000",
      textAlign: "center",
      padding: "10px 0",
      zIndex: 1000,
      fontWeight: "bold",
      backdropFilter: "blur(2px)",
    }}
  >
    Esta es una versin demo. Los datos y funcionalidades son simulados.
  </div>
);

//Componente raz de la aplicacin que configura:
//- El enrutador de React Router
//- El proveedor de contexto de autenticacin
//- La estructura general de navegacin

function App() {
  const [demoUser, setDemoUser] = useState(null);

  useEffect(() => {
    const fetchDemoUser = async () => {
      const user = await getDemoUser();
      setDemoUser(user);
    };
    fetchDemoUser();
  }, []);

  if (!demoUser) {
    return <div>Cargando demo...</div>; // Pantalla de carga mientras se obtienen los datos
  }

  return (
    <Router>
      {/* Proveedor de contexto de autenticacin para toda la aplicacin */}
      <AuthProvider>
        {/* Componente que restablece el scroll al cambiar de pgina */}
        <ScrollToTop />
        {/* Header comn para todas las pginas */}
        <Header user={demoUser} />

        <Routes>
          {/* Rutas pblicas - Accesibles para todos los usuarios */}
          <Route path="/" element={<HomePage />} />
          <Route path="/conocenos" element={<Conocenos />} />
          <Route path="/blog" element={<Blog />} />
          <Route path="/ayuda" element={<Ayuda />} />
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/admin/login" element={<AdminLogin />} />

          {/* Rutas protegidas - Solo para usuarios autenticados */}
          <Route
            path="/perfil"
            element={
              <ProtectedRoute>
                <PerfilVista />
              </ProtectedRoute>
            }
          />
          <Route
            path="/perfil/editar"
            element={
              <ProtectedRoute>
                <Perfil />
              </ProtectedRoute>
            }
          />
          <Route
            path="/matches"
            element={
              <ProtectedRoute>
                <MatchPage />
              </ProtectedRoute>
            }
          />
          <Route
            path="/comunidad"
            element={
              <ProtectedRoute>
                <Comunidad />
              </ProtectedRoute>
            }
          />
          <Route
            path="/comunidad/foro"
            element={
              <ProtectedRoute>
                <Forum />
              </ProtectedRoute>
            }
          />
          <Route
            path="/comunidad/trueques"
            element={
              <ProtectedRoute>
                <Trueque />
              </ProtectedRoute>
            }
          />
          <Route
            path="/mensajes/trueques"
            element={
              <ProtectedRoute>
                <TruequeMessages />
              </ProtectedRoute>
            }
          />
          <Route
            path="/comunidad/eventos"
            element={
              <ProtectedRoute>
                <Events />
              </ProtectedRoute>
            }
          />
          <Route
            path="/admin/dashboard"
            element={
              <ProtectedRoute>
                <AdminDashboard />
              </ProtectedRoute>
            }
          />

          {/* Ruta para cualquier URL no definida - Pgina 404 */}
          <Route path="*" element={<NotFound />} />
        </Routes>
        <DemoBanner />
      </AuthProvider>
    </Router>
  );
}

// Configuracin global para el comportamiento de scroll
if (typeof window !== "undefined") {
  window.history.scrollRestoration = "manual";
}

export default App;
