import React from "react";

// Componente para mostrar mensajes de xito, error o informacin en cualquier parte del mdulo de perfil.
// Intencin de reutilizarlo en diferentes lugares.
// Los estilos CSS que cambian segn tipo de mensaje (success, error, info) que se pasa por props.

const MessageDisplay = ({ message }) => {
  if (!message || !message.text) return null;

  return <div className={`message ${message.type}`}>{message.text}</div>;
};

export default MessageDisplay;
