import { config } from "../../config";

const BACKEND_URL = "http://localhost/mommatch/backend";

// Este archivo contiene la lgica del sistema de matching.
// Separ todas estas funciones del componente principal para mantenerlo ms limpio
// Los mensajes temporales dan feedback a las usuarias.

export const handleLike = async (
  userId,
  profiles,
  setProfiles,
  setMatches,
  setLikedProfiles,
  setMessage,
  fetchRealMatches,
  BACKEND_URL,
  setShowMatchCelebration,
  setCurrentMatch
) => {
  // Modo demo: simular funcionalidad sin backend
  if (config.useMocks) {
    const likedProfile = profiles.find((profile) => profile.user_id === userId);
    
    if (!likedProfile) return;
    
    // Remover el perfil de la lista principal
    setProfiles((prev) => prev.filter((profile) => profile.user_id !== userId));
    
    // 30% de probabilidad de match instantneo en demo
    const isMatch = Math.random() < 0.3;
    
    if (isMatch) {
      // Es un match mutuo
      setMatches((prev) => [...prev, likedProfile]);
      setCurrentMatch(likedProfile);
      setShowMatchCelebration(true);
    } else {
      // Solo like pendiente
      setLikedProfiles((prev) => [...prev, likedProfile]);
      setMessage({
        type: "info",
        text: "Has dado 'Conectemos' a este perfil",
      });
      setTimeout(() => setMessage(null), 3000);
    }
    return;
  }

  try {
    const response = await fetch(`${BACKEND_URL}/register_match.php`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify({ target_user_id: userId, action: "like" }),
    });

    const data = await response.json();
    if (data.success) {
      setProfiles((prev) => prev.filter((profile) => profile.id !== userId));

      if (data.message && data.message.includes("match")) {
        // En lugar de mostrar un mensaje simple, se muestra la celebracin del "MomMatch"
        const matchedProfile = profiles.find(
          (profile) => profile.id === userId
        );

        if (matchedProfile) {
          setMatches((prev) => [...prev, matchedProfile]);
          // Guardo perfil del match y se activa la celebracin
          setCurrentMatch(matchedProfile);
          setShowMatchCelebration(true);
        }

        fetchRealMatches();
      } else {
        const likedProfile = profiles.find((profile) => profile.id === userId);
        if (likedProfile) {
          setLikedProfiles((prev) => [...prev, likedProfile]);
        }

        setMessage({
          type: "info",
          text: "Has dado 'Conectemos' a este perfil",
        });
        setTimeout(() => setMessage(null), 3000);
      }
    } else {
      console.error(data.error || "Error al marcar Me gusta.");
      setMessage({
        type: "error",
        text: "Error al procesar tu accin",
      });
      setTimeout(() => setMessage(null), 3000);
    }
  } catch (error) {
    console.error("Error al enviar Me gusta:", error);
    setMessage({
      type: "error",
      text: "Error de conexin",
    });
    setTimeout(() => setMessage(null), 3000);
  }
};

export const handleReject = async (
  userId,
  profiles,
  setProfiles,
  setRejectedProfiles,
  setMessage,
  BACKEND_URL
) => {
  // Modo demo: simular funcionalidad sin backend
  if (config.useMocks) {
    const rejectedProfile = profiles.find((profile) => profile.user_id === userId);
    
    if (!rejectedProfile) return;
    
    // Remover el perfil de la lista principal
    setProfiles((prev) => prev.filter((profile) => profile.user_id !== userId));
    
    // Agregar a rechazados
    setRejectedProfiles((prev) => [...prev, rejectedProfile]);
    
    setMessage({
      type: "info",
      text: "Perfil guardado en 'Tal vez luego'",
    });
    setTimeout(() => setMessage(null), 3000);
    return;
  }

  try {
    const rejectedProfile = profiles.find((profile) => profile.id === userId);

    const response = await fetch(`${BACKEND_URL}/register_match.php`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify({ target_user_id: userId, action: "reject" }),
    });

    const data = await response.json();
    if (data.success) {
      setProfiles((prev) => prev.filter((profile) => profile.id !== userId));

      if (rejectedProfile) {
        setRejectedProfiles((prev) => [...prev, rejectedProfile]);
        setMessage({
          type: "info",
          text: "Perfil movido a 'Tal vez luego'",
        });
        setTimeout(() => setMessage(null), 3000);
      }
    } else {
      console.error(data.error || "Error al rechazar usuario.");
    }
  } catch (error) {
    console.error("Error al enviar rechazo:", error);
  }
};

export const reinsertProfile = async (
  profileId,
  rejectedProfiles,
  setProfiles,
  setRejectedProfiles,
  setMessage
) => {
  // Modo demo: simular funcionalidad sin backend
  if (config.useMocks) {
    const profileToReinsert = rejectedProfiles.find((profile) => profile.user_id === profileId);
    
    if (!profileToReinsert) return;
    
    // Remover de rechazados
    setRejectedProfiles((prev) => prev.filter((profile) => profile.user_id !== profileId));
    
    // Agregar de nuevo a la lista principal
    setProfiles((prev) => [...prev, profileToReinsert]);
    
    setMessage({
      type: "success",
      text: "Perfil reinsertado correctamente.",
    });
    setTimeout(() => setMessage(null), 3000);
    return;
  }

  try {
    const response = await fetch(`${BACKEND_URL}/reinsert_profile.php`, {
      method: "POST",
      credentials: "include",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ profile_id: profileId }),
    });

    if (!response.ok) {
      throw new Error(`Error HTTP: ${response.status}`);
    }

    const data = await response.json();

    if (data.success) {
      // Actualiza los estados eliminando el perfil de rechazados y aadindolo a candidatos
      const updatedRejectedProfiles = rejectedProfiles.filter(
        (profile) => profile.id !== profileId
      );
      setRejectedProfiles(updatedRejectedProfiles);
      setProfiles((prevProfiles) => [...prevProfiles, data.profile]);
      setMessage({
        type: "success",
        text: "Perfil reinsertado correctamente.",
      });

      // Aadir temporizador para limpiar el mensaje despus de 3 segundos
      setTimeout(() => setMessage(null), 3000);
    } else {
      throw new Error(data.error || "Error desconocido.");
    }
  } catch (error) {
    console.error("Error al reinsertar perfil:", error);
    setMessage({
      type: "error",
      text: "Hubo un problema al reinsertar el perfil. Por favor, intntalo de nuevo.",
    });

    // Tambin aadir temporizador para el mensaje de error
    setTimeout(() => setMessage(null), 3000);
  }
};

export const removeLikedProfile = async (
  profileId,
  setLikedProfiles,
  setMessage,
  BACKEND_URL
) => {
  // Modo demo: simular funcionalidad sin backend
  if (config.useMocks) {
    setLikedProfiles((prev) =>
      (prev || []).filter((profile) => profile.user_id !== profileId)
    );
    
    setMessage({
      type: "success",
      text: "Perfil eliminado de 'Conectemos' correctamente",
    });
    setTimeout(() => setMessage(null), 3000);
    return;
  }

  try {
    const response = await fetch(`${BACKEND_URL}/register_match.php`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify({
        target_user_id: profileId,
        action: "remove_like",
      }),
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = await response.json();
    if (data.success) {
      setLikedProfiles((prev) =>
        (prev || []).filter((profile) => profile.id !== profileId)
      );

      setMessage({
        type: "success",
        text: "Perfil eliminado de 'Me gusta' correctamente",
      });
      setTimeout(() => setMessage(null), 3000);
    } else {
      throw new Error(data.error || "Error al eliminar el perfil");
    }
  } catch (error) {
    console.error("Error al eliminar perfil de 'Me gusta':", error);
    setMessage({
      type: "error",
      text: "Error al eliminar el perfil. Por favor, intntalo de nuevo.",
    });
    setTimeout(() => setMessage(null), 3000);
  }
};
