import React from "react";

// Este componente maneja el campo de presentacin
// Implement un contador de caracteres inteligente que cambia de color
// segn se acerca al lmite (amarillo al 75%, rojo al 90%).
// Esta pequea caracterstica da un feedback visual muy til a las
// usuarias sin necesidad de instrucciones adicionales. Quera que
// fuera intuitivo y que no tuvieran sorpresas al intentar guardar
// el formulario por exceder el lmite

const PresentationField = ({
  presentation,
  charCount,
  maxChars,
  onInputChange,
}) => {
  return (
    <div className="form-group highlight-field presentation-field">
      <label htmlFor="presentation" className="highlight-label">
        Presntate a las otras mams
      </label>
      <textarea
        id="presentation"
        name="presentation"
        value={presentation || ""}
        onChange={onInputChange}
        className="form-input"
        placeholder="Escribe una breve presentacin sobre ti para que las dems mams te conozcan mejor (mximo 500 caracteres)"
        rows="4"
        maxLength={maxChars}
      ></textarea>
      <div
        className={`char-counter ${
          (charCount / maxChars) * 100 >= 90
            ? "danger"
            : (charCount / maxChars) * 100 >= 75
            ? "warning"
            : ""
        }`}
      >
        {charCount}/{maxChars} caracteres
      </div>
    </div>
  );
};

export default PresentationField;
