import { config } from "../../config";

const BACKEND_URL = config.useMocks ? null : "http://localhost/mommatch/backend";

// Este archivo centraliza todas las utilidades, constantes y funciones auxiliares
// que se utilizan en los componentes del mdulo de perfil.
//--
// La funcin de formatPhotoUrl me dio bastantes problemas.
// Por eso cre esta funcin que maneja todos los casos y adems permite forzar
// una recarga aadiendo un timestamp cuando es necesario.
//--
// Tambin inclu aqu todas las constantes como los tipos de familia y condiciones
// especiales para no repetirlas en cada componente. Si necesitamos aadir nuevas
// opciones, solo hay que modificar este archivo.

/**
 * Formatea la URL de una foto de perfil, aadiendo timestamp solo cuando es necesario
 * @param {string} photoPath La ruta de la foto
 * @param {boolean} forceRefresh Si es true, fuerza una recarga aadiendo timestamp
 * @returns {string} URL formateada
 */
export const formatPhotoUrl = (photoPath, forceRefresh = false) => {
  if (!photoPath) {
    if (config.useMocks) {
      return `${process.env.PUBLIC_URL}/uploads/profiles/default_profile.jpg`;
    }
    return `${BACKEND_URL}/public/uploads/profiles/default_profile.jpg`;
  }

  if (photoPath.startsWith("http")) {
    return photoPath;
  }

  // En modo demo, construir URL con PUBLIC_URL
  if (config.useMocks) {
    if (photoPath.startsWith("/public/uploads/profiles/")) {
      return `${process.env.PUBLIC_URL}${photoPath.replace('/public', '')}`;
    } else if (photoPath.startsWith("/public/uploads/")) {
      return `${process.env.PUBLIC_URL}${photoPath.replace('/public', '')}`;
    } else if (photoPath.startsWith("public/uploads/profiles/")) {
      return `${process.env.PUBLIC_URL}/${photoPath.replace('public/', '')}`;
    } else if (photoPath.startsWith("/uploads/")) {
      return `${process.env.PUBLIC_URL}${photoPath}`;
    }
    return photoPath;
  }

  // Construir la URL base segn el formato de la ruta (modo desarrollo)
  let baseUrl = "";
  if (photoPath.startsWith("/public/uploads/profiles/")) {
    baseUrl = `${BACKEND_URL}${photoPath}`;
  } else if (photoPath.startsWith("/public/uploads/")) {
    baseUrl = `${BACKEND_URL}${photoPath}`;
  } else if (photoPath.startsWith("public/uploads/profiles/")) {
    baseUrl = `${BACKEND_URL}/${photoPath}`;
  } else {
    baseUrl = photoPath;
  }

  // Aadir timestamp solo cuando se requiere forzar una recarga
  return forceRefresh ? `${baseUrl}?t=${new Date().getTime()}` : baseUrl;
};

export const verifySession = async () => {
  try {
    const response = await fetch(
      "http://localhost:3000/mommatch/backend/verify_session.php",
      {
        credentials: "include",
        headers: { "Content-Type": "application/json" },
      }
    );

    const data = await response.json();

    if (!data.authenticated) {
      throw new Error("Sesin no vlida");
    }
  } catch (error) {
    console.error("Error verificando la sesin:", error.message);
  }
};

export const BACKEND_URL_CONSTANT = BACKEND_URL;

